export default {
	data(){
		return{
			url:'',
			listorder:[]
		}
	},
	onShow() {
		this.getData();
		this.url = this.$H.common.ul;
	},
	methods:{
		onLoad(){
		},
		toallorder(){
			uni.navigateTo({
				    url: '../../pages/mannerorder/allorder',
			
			});
		},
		getData(){
			var that = this
			this.$H.get('/mannerdish/order/todayorder',{},{
				token:true,
				native:true
			}).then(res=>{
				console.log(res.data.data)
				that.$data.listorder = res.data.data
			}).catch(err=>{ //支付失败
				this.loading = false
				uni.showToast({
					title: '支付失败',
					icon: 'none'
				});
		    })
		}
	},
}
